*{
 margin: 0;
 padding: 0;
 box-sizing: border-box;
}

form{
  max-width: 1200px;
  padding: 1rem;
  margin: auto;
}
form input{
  width: 50%;
  font-size: 30px;
  font-weight: bolder;
  background-color: black;
  color: blueviolet;
  border: none;
}
form  button{
  width: 20%;
  font-size: 30px;
  font-weight: bolder;
  background-color: blueviolet;
  color: white;
  border: none;
  cursor: pointer;
}
li{
  margin: auto;
  width: 50%;
}
li span{
  width: 300%;

}
